@import "../../../styles/utilities/index";

main-sidebar {
  @include flexbox(column, flex-start, stretch, nowrap, 0, 0, auto);

  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  @media (min-width: $breakpoint-mobile-med) {
    width: $sidebar-desktop-width;
  }

  background-color: white;
  border-right: 1px solid $grey-13-5;

  z-index: 99;
}

.__wrapper {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 0, auto);
}

.__toolbar {
  @include flexbox(column, center, center, nowrap, 0, 0, auto);
  height: $toolbar-height;
  padding: 0;
  border-bottom: 1px solid $grey-13-5;
  text-align: center;

  &__title {
    font-weight: bold;
    letter-spacing: -0.02em;
  }

}

.__icon {
  position: relative;
  height: 2.5em;
  user-select: none;
  pointer-events: none;
}

.__button {
  &--border-top {
    border-top: 1px solid $grey-13-5 !important;
  }
}

.__buttons {
  @include flexbox(column, flex-start, stretch, nowrap, 0, 0, auto);

  > button {
    align-self: flex-start;
    width: 100%;
    text-align: left;
    padding-left: 1.5em;
    &:last-child {
      border-bottom: 1px solid $grey-13-5 !important;
    }
  }

  &--bottom {
    margin-top: auto;
  }
}

.__version {
  font-size: 0.75em;
  padding: $padding-default;
  text-align: center;

  color: $grey-12;
}























