@import "../../../../styles/utilities/index";

.__logo {
  width: 100%;
  padding: 0 0 0.5em;
  align-self: center;

  min-height: calc((100vw - 2 * #{$padding-side}) / 2.77);

  @media (min-width: $breakpoint-mobile-med) {
    min-height: inherit;
    padding: 0 0 0.6em;
  }

  flex-shrink: 0; // ie 11 bug
}

$square-logo-size: 63px;
$square-logo-padding: 1em;
$square-logo-border-radius: 2px;

.__square-logo {
  width: $square-logo-size;
  align-self: center;
  height: $square-logo-size;
  min-height: $square-logo-size;
  flex-shrink: 0;

}

.__logo-banner {
  @include flexbox(row, center, stretch, nowrap, 0, 0, auto);
  padding: $padding-default;
  background: linear-gradient(38deg, #28aae1 0%, #0070bb 100%);
}

.__logo-wrapper {
  padding: $square-logo-padding;
  width: calc(#{$square-logo-size} + #{2*$square-logo-padding} + #{2*$square-logo-border-radius});
  height: calc(#{$square-logo-size} + #{2*$square-logo-padding} + #{2*$square-logo-border-radius});

  background: white;
  border: $square-logo-border-radius solid $blue;
  border-radius: 50%;
}

.__greetings {
  @include flexbox(column, center, center, nowrap, 0, 0, auto);
  font-size: 1.1em;
  padding: 2 * $padding-top 0 $padding-top;
  font-weight: bold;
  > * {
    text-align: center;
  }
}


