@import "../../../shared/styles/animations/index";

@keyframes tap-effect-in {
  0% {
    background-color: rgba(0, 0, 0, 0);
  }
  80% {
    background-color: transparentize(white, 0.5);
  }
  100% {
    background-color: transparentize(white, 0.5);
  }
}

@keyframes tap-effect-out {
  0% {
    background-color: transparentize(white, 0.5);
  }
  100% {
    background-color: rgba(0, 0, 0, 0);
  }
}

@mixin tap-effect($color) {
  pointer-events: all;
  touch-action: manipulation;
  overflow: visible;

  $offset: -1px;

  &::before {
    content: '';
    position: absolute;
    top: $offset;
    bottom: $offset;
    left: $offset;
    right: $offset;
    border-radius: 2px;
    pointer-events: none;
    touch-action: manipulation;
    z-index: 1;

    animation: tap-effect-out $tap-effect-out-duration normal $swift-ease-out-timing-function 0ms forwards;
  }

  &:active {
    &::before {
      animation: tap-effect-in $tap-effect-in-duration normal $swift-ease-out-timing-function 0ms forwards;
    }
  }
}


