@import "../../../styles/utilities/index";

@keyframes __enter {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  50% {
  }
  90% {
    z-index: 0;
  }
  to {
    transform: translateY(0px);
    z-index: 1;
    opacity: 1;
  }
}

@keyframes __enter--mobile {
  from {
    opacity: 1;
    height: 0;
  }
