    height: $on-screen-notification-height;
    box-shadow: none;
  }
  20% {
    opacity: 0;
    z-index: 1;
    height: $on-screen-notification-height;
  }
  to {
    opacity: 0;
    z-index: 1;
    height: 0;
  }
}

@keyframes __leave--mobile {
  from {
    opacity: 1;
    height: $on-screen-notification-height;
  }
  to {
    opacity: 0;
    height: 0;
  }
}

.__leave {
  animation: __leave--mobile $swift-ease-out-timing-function normal $swift-ease-out-duration 0ms 1 forwards;

  @media (min-width: $breakpoint-mobile-med) {
    animation: __leave $swift-ease-out-timing-function normal $swift-ease-out-duration 0ms 1 forwards;
  }
}

.__enter {
  animation: __enter--mobile $swift-ease-in-timing-function normal $swift-ease-in-duration-short 1 forwards;

  @media (min-width: $breakpoint-mobile-med) {
