@import "styles/utilities/_index";

@keyframes __enter-left {
  0% {
    opacity: 1;
    z-index: 1;
    transform: translateX(-35%);
  }
  100% {
    opacity: 1;
    z-index: 1;
    transform: translateX(0);
  }
}

@keyframes __fade-in {
  0% {
    opacity: 0;
    z-index: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 1;
    z-index: 1;
    transform: translateX(0);
  }
}

@keyframes __fade-out {
  0% {
    opacity: 1;
    z-index: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    z-index: 1;
    transform: translateX(0);
  }
}

@keyframes __fade-out-opacity {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.9;
  }
}

@keyframes __fade-in-opacity {
  0% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@keyframes __enter-right {
  0% {
    opacity: 1;
    z-index: 2;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    z-index: 2;
    transform: translateX(0);
  }
}

@keyframes __leave-left {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 1;
    transform: translateX(-35%);
  }
}

@keyframes __leave-right {
  0% {
    opacity: 1;
    z-index: 2;
    transform: translateX(0);
  }
  100% {
    opacity: 1;
    z-index: 2;
    transform: translateX(100%);
  }
}

.__leave-right {
  > * {
    animation: __leave-right $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  }
}

.__leave-left {
  animation: __fade-out-opacity $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  > * {
    animation: __leave-left $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  }
}

.__enter-right {
  > * {
    animation: __enter-right $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  }
}

.__enter-left {
  animation: __fade-in-opacity $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  > * {
    animation: __enter-left $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  }
}

.__fade-in {
  > * {
    animation: __fade-in $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  }
}

.__fade-out {
  > * {
    animation: __fade-out $swift-ease-out-timing-function normal $ui-router-transition-duration 0ms forwards;
  }
}

.__instant-fade-in {
  > * {
    opacity: 1;
    z-index: 1;
    transform: translateX(0);
  }
}

.__instant-fade-out {
  > * {
    opacity: 0;
    z-index: 1;
    transform: translateX(0);
  }
}

// fix tap-effect flashing when we do a instant fade in
:global {
  .app-animations__tap-effect-cancel {
    a:not(:active), button.pure-button:not(:active) {
      &::before {
        visibility: hidden;
      }
    }
  }
}



