@import "../../styles/utilities/index";

@keyframes __enter {
  from {
    z-index: 1;
    opacity: 0;
    visibility: visible;
  }
  to {
    z-index: 1;
    opacity: 1;
    visibility: visible;
  }
}

@keyframes __leave {
  0% {
    z-index: 1;
    visibility: visible;
    opacity: 1;
  }
  90% {
    z-index: 1;
    opacity: 0;
    visibility: visible;
  }
  100% {
    z-index: 0;
    opacity: 0;
    visibility: hidden;
  }
}

.__leave {
  animation: __leave $swift-ease-in-timing-function normal 200ms 0ms forwards;
}

.__leave-instant {
  z-index: 0;
  opacity: 0;
  visibility: hidden;
}

.__enter {
  animation: __enter $swift-ease-out-timing-function normal 200ms 0ms forwards;
}

.__enter-instant {
  z-index: 1;
  opacity: 1;
  visibility: visible;
}



