@import "../../../styles/utilities/index";

main-tabs {
  @include flexbox(column, flex-start, stretch, nowrap, 0, 0, auto);

  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
  z-index: 99;
}

.--ios-chrome {
  bottom: 20px;
}

.__wrapper {
  @include flexbox(row, flex-start, stretch, nowrap, 1, 0, auto);
  height: calc(#{$tabs-height} + 1px);
  background: white;
  > .__button {
    font-size: 0.7em;
  }
}

.__button {
  flex-grow: 1;
  width: 25%;
  height: 100%;
  padding: 0;
  > i {
    font-size: 1.74em;
    position: absolute;
    margin-right: 0;
    left: calc(50% - 0.5em);
    top: 0.3em;
  }
  > span {
    display: inline;
    vertical-align: -76%;
    line-height: 3.3em;
    font-weight: normal;
    letter-spacing: 0.03em;
    font-size: 0.95em;
  }
}

:global {
  .main-tabs__button.pure-button-default:not(.pure-button-primary) {
    border-top: 1px solid #ececec;
  }

  .main-tabs__button.pure-button-primary {
    border-top: 1px solid darken($primary, 5%) !important;
  }
}




