@import "../../../../shared/styles/partials/_tiles";
@import "../../../styles/utilities/index";

.__wrapper {
  position: relative;

  @include flexbox(column, center, stretch, nowrap, 1, 0, auto);

  margin-top: -$toolbar-height;
  background: white;
}

.__form {
  @include flexbox(column, center, stretch, nowrap, 0, 0, auto);
  padding: 0 $padding-side;
}

.__social-buttons {
  @include flexbox(row, space-between, center, nowrap, 1, 0, auto);
  padding: 0 $padding-side $padding-top;

  > button {
    width: calc(50% - #{$padding-side/3});
    margin: 0.5em 0 1em;
  }
}




