@import "../../../styles/utilities/index";
@import "../../../../shared/styles/partials/scroll";

on-screen-notifications {
  font-size: 0.8em;

  position: fixed;
  top: 0;
  right: 0;
  left: 0;

  @media (min-width: $breakpoint-mobile-med) {
    bottom: inherit;
    left: inherit;
    //right: calc(25% - 150px);
    right: 100px;
    top: 15px;
  }

  z-index: 100;
  //transform: translate3d(0px, 0px, 0px); // z axis will make text blurry on ie11
  will-change: transform;
}

.__wrapper {
  background: rgba(0, 0, 0, 0);
}

.__notification {
  position: relative;
  display: block;
  overflow: hidden;
  will-change: opacity, height, transform;

  transform: translateY(0px);

  width: 100%;
  height: $on-screen-notification-height;

  z-index: 0;

  background: white;
  color: white;

  @media (min-width: $breakpoint-mobile-med) {
    width: 320px;
    box-shadow: 0 3px 13px 2px rgba(152, 152, 152, 0.53);
  }

}

.__message {
  display: inline-block;
  vertical-align: middle;

  font-size: 1.1em;

  height: auto;
  max-width: calc(100% - 105px);

  margin: 0;
  text-overflow: ellipsis;
  overflow-wrap: break-word;
  white-space: normal;
  overflow-x: hidden;

  &--long {
    max-width: calc(100% - 60px);
  }
}

.__danger {
  background: darken($danger, 10%);
}

.__warning {
  background: $warning;
}

.__primary {
  background: $primary;
}

.__blue-green {
  background: $blue-green-darker
}

.__default {
  border: 1px solid $grey-12;
  background-color: white;
  color: $base;
}

.__success {
  background: $success;
}

.__close {
  background: rgba(1, 40, 66, 0.28) !important;
  position: absolute !important;
  right: 0;
  bottom: 0;
  top: 0;
}

.__icon-wrapper {
  display: inline-block;
  vertical-align: middle;

  height: $on-screen-notification-height;
  width: auto;
  padding: calc(#{$on-screen-notification-height/2} - 11px) 8px calc(#{$on-screen-notification-height/2} - 11px) 14px;

  &::before {
    font-size: 1.7em;
  }
}

.__icon {
  margin: 0 0 0 0;
  &::before {
    font-size: 1.7em;
  }
}


