@import "../../../styles/utilities/index";

main-toolbar {
  @include flexbox(row, center, center, nowrap, 0, 0, auto);
  position: relative;
  top: 0;
  left: 0;
  right: 0;

  @media (min-width: $breakpoint-laptop-small) {
    padding-left: $sidebar-desktop-width;
  }

  height: $toolbar-height;
  padding: 0.15em 0 0 0;

  z-index: 97;

  background-color: transparentize(white, 0.08);
  border-bottom: 1px solid $grey-13-5;
}

.__back-button {
  position: absolute !important;
  left: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  background: transparent !important;
}

.__refresh-button {
  position: absolute !important;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  background: transparent !important;
}

.__menu-button {
  position: absolute !important;
  left: 0;
  top: 0;
  bottom: 0;

  height: 100%;
  padding-right: 3.7em;
  padding-top: 0.95em;
  @media (min-width: $breakpoint-mobile-med) {
    padding-left: 1.2em;
    padding-top: 0.90em;
  }

  > i {
    font-size: 1.233333em !important;
    color: $base;
    //font-size: 1.533333em !important;
  }
}

.__title {
  font-weight: bold;
  letter-spacing: -0.02em;
}




