@import "../shared/styles/partials/_tiles";
@import "../shared/styles/partials/_scroll";
@import './styles/utilities/index';

:global {
  // TODO move to shame
  body {
    &.hide-one-signal {
      #onesignal-bell-container {
        visibility: hidden;
      }
    }
  }
  // overrides default
  #onesignal-bell-container.onesignal-reset.onesignal-bell-container-bottom-right {
    z-index: 99;
    bottom: inherit !important;
    top: 80px !important;
    .onesignal-bell-launcher-dialog {
      &.onesignal-bell-launcher-dialog-opened {
        transform: translateY(137%) !important;
      }
      &:before, &:after {
        visibility: hidden !important;
      }
    }
  }
}

app {
  @include flexbox(row, flex-start, stretch, nowrap, 1, 0, 100%);
}

.__wrapper {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 0, auto);
  position: relative;
}

.__ui-view {

  opacity: 1;

  > * {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background: #fff;
    transform: translateX(100%);
    z-index: 1;
    opacity: 0;

    @include scroll();
    overflow-y: scroll;
    overflow-x: hidden;

    box-shadow: -2px 1px 20px 1px rgba(101, 101, 101, 0.29);

    > * {
      height: calc(100% + 2px);
      min-height: calc(100% + 2px);
      @media (min-width: $breakpoint-mobile-med) {
        height: inherit;
        min-height: inherit;
      }
    }
  }
}

// ***
// look in app controller for the add/remove of those classes,
// they are used to give consistent look for slido embeds
.__html--white {
  background: white;
}

.__html--grey {
  background: $grey-13-75;
}

