@import "../utilities/index";

:global {
  .pure-button {
    /* Structure */
    position: relative;
    display: inline-block;
    zoom: 1;
    line-height: normal;
    white-space: nowrap;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    @include text-shadow-button();
    flex-shrink: 0;
  }

  /* Firefox: Get rid of the inner focus border */
  .pure-button::-moz-focus-inner {
    padding: 0;
    border: 0;
  }

  /*csslint outline-none:false*/

  .pure-button {
    font-family: inherit;
    font-size: 100%;
    font-weight: bold;
    padding: $padding-button;
    color: white; /* rgba not supported (IE 8) */
    color: rgba(0, 0, 0, 0.80); /* rgba supported */
    border: 1px solid darken($grey-13, 40%); /*IE9 + everything else*/
    background-color: $grey-13;
    text-decoration: none;
    border-radius: $border-radius-button;
    //font-size: 1.2em;
  }

  //.pure-button-hover,
  //.pure-button:hover,
  //.pure-button:focus {
  //  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#1a000000', GradientType=0);
  //  background-image: -webkit-gradient(linear, 0 0, 0 100%, from(transparent), color-stop(40%, rgba(0, 0, 0, 0.05)), to(rgba(0, 0, 0, 0.10)));
  //  background-image: -webkit-linear-gradient(transparent, rgba(0, 0, 0, 0.05) 40%, rgba(0, 0, 0, 0.10));
  //  background-image: -moz-linear-gradient(top, rgba(0, 0, 0, 0.05) 0%, rgba(0, 0, 0, 0.10));
  //  background-image: -o-linear-gradient(transparent, rgba(0, 0, 0, 0.05) 40%, rgba(0, 0, 0, 0.10));
  //  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.05) 40%, rgba(0, 0, 0, 0.10));
  //}

  .pure-button:focus {
    outline: 0;
  }

  .pure-button-active,
  .pure-button:active {
    //@include box-shadow-button-active();
    //border-color: $base;
  }

  .pure-button[disabled],
  .pure-button-disabled,
  .pure-button-disabled:hover,
  .pure-button-disabled:focus,
  .pure-button-disabled:active {
    //border: none;
    background-image: none;
    //filter: progid:DXImageTransform.Microsoft.gradient(enabled = false);
    //filter: alpha(opacity=40);
    //-khtml-opacity: 0.40;
    //-moz-opacity: 0.40;
    //opacity: 0.40;
    border: 1px solid darken($grey-12, 5%);
    background-color: $grey-12;
    color: $grey-13-5;
    text-shadow: none;

    cursor: not-allowed;
    box-shadow: none;
  }

  .pure-button-hidden {
    display: none;
  }

  /* Firefox: Get rid of the inner focus border */
  .pure-button::-moz-focus-inner {
    padding: 0;
    border: 0;
  }

  .pure-button-default,
  a.pure-button-default {
    border: 1px solid $grey-6;
    background-color: white;
    color: $primary;
    @include tap-effect($grey-9);
    text-shadow: none;
  }

  .pure-button-primary,
  .pure-button-selected,
  a.pure-button-primary,
  a.pure-button-selected {
    transition: box-shadow 0.2s ease-out;
    border: 1px solid darken($primary, 5%);
    background-color: $primary;
    color: white;
    @include tap-effect($primary);
  }

  .pure-button-success,
  a.pure-button-success {
    transition: box-shadow 0.2s ease-out;
    border: 1px solid darken($success, 5%);
    background-color: $success;
    color: white;
    @include tap-effect($success);
  }

  .pure-button-warning,
  a.pure-button-warning {
    border: 1px solid darken($warning, 5%);
    background-color: $warning;
    color: white;
    @include tap-effect($warning);
  }

  .pure-button-blue-green,
  a.pure-button-blue-green {
    border: 1px solid darken($blue-green-darker, 5%);
    background-color: $blue-green-darker;
    color: white;
    @include tap-effect($blue-green-darker);
  }

  .pure-button-danger,
  a.pure-button-danger {
    border: 1px solid darken($danger, 5%);
    background-color: $danger;
    color: white;
    @include tap-effect($danger);

  }

  .pure-button-success,
  a.pure-button-success {
    border: 1px solid darken($success, 5%);
    background-color: $success;
    color: white;
    @include tap-effect($success);
  }

  .pure-button-group-inline {
    border-radius: 0 0 0 0;
    > button:not(:last-child) {
      border-right: none;
    }
    > button:first-child {
      margin-right: 0;
      border-radius: $border-radius-button 0 0 $border-radius-button;
      position: relative;
      float: left;
    }
    > button:last-child {
      border-radius: 0 $border-radius-button $border-radius-button 0;
      position: relative;
      float: left;
    }
  }

  .pure-button.pure-button--square-corner {
    border-radius: 0;
  }

  .pure-button.pure-button--no-border {
    border: none;
  }

  .pure-button.pure-button--no-text-shadow {
    text-shadow: none;
  }

  .pure-button.pure-button--xsmall {
    font-size: 70%;
  }

  .pure-button.pure-button--small {
    font-size: 85%;
  }

  .pure-button.pure-button--large {
    font-size: 110%;
  }

  .pure-button.pure-button--xlarge {
    font-size: 125%;
  }

  .pure-button i {
    display: inline-block;
    font-size: 1em;
    line-height: 0.75em;
    vertical-align: -20%;
    margin-right: 14px;
  }

  .pure-button.pure-button--no-icon-margin i {
    margin-right: 0;
  }

  .pure-button.pure-button--navigation i {
    display: inline-block;
    font-size: 1.3333333333333333em;
    line-height: 0.75em;
    vertical-align: -15%;
    margin-right: 0;
  }

  .pure-button.pure-button--navigation {
    letter-spacing: -0.03em;
    font-weight: normal;
  }

  .pure-button.pure-button--back-button {
    padding: 0.35em 0.8em 0 0.3em;
    @media (min-width: $breakpoint-mobile-med) {
      padding: 0.3em 0.8em 0 0.3em;
    }

    &.pure-button--back-button--ios {
      padding: 0.25em 0.8em 0 0.3em;
    }
  }

  .pure-button.pure-button--back-button i.icon-angle-left {
    font-size: 1.933333em;
    line-height: 1.1em;
    vertical-align: -25%;
    width: 0.80em;
  }

  .pure-button.pure-button--back-button i.icon-go-back-left-arrow {
    padding-top: 0;
    font-size: 1.2em;
    line-height: 1em;
    vertical-align: -20%;
    width: 35px;
  }
}





