@import "../../../shared/styles/partials/_tiles";
@import "../../styles/utilities/index";

login, reset-password, sign-up, login-email {
  @extend .tile;
  @include flexbox(column, flex-start, stretch, nowrap, 1, 1, auto);

  position: relative;

  overflow-x: hidden;
  overflow-y: hidden;

  background: white;

  $auth-height: 85vh;

  @media (min-width: $breakpoint-mobile-med) {
    bottom: inherit !important;
    align-self: center;

    max-height: $auth-height;

    max-width: 420px;
    min-height: 495px;
    width: 420px;

    margin: 5vh auto auto;

    overflow: auto !important;

    @media (min-height: $breakpoint-tablet-height) {
      max-height: inherit;
      margin: 10vh auto auto;
    }
  }
}

.__column {
  @include flexbox(column, center, stretch, nowrap, 1, 0, auto);
}

.__separator {
  @include flexbox(column, center, center, nowrap, 0, 0, auto);
  position: relative;
  margin: $padding-top 0;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    bottom: 50%;
    border-top: 1px solid $grey-13;
  }
}

.__separator-text {
  font-size: 0.9em;
  padding: 0 0.9em;
  background: white;
  z-index: 1;
}

.__link {
  text-align: right;
  cursor: pointer;
  font-size: 0.9em;
  margin: 0.5em 0 0.6em;
}

.__prompt {
  margin: 0.5em 0 1.1em;
  padding: 0 $padding-side;
  @include flexbox(column, center, stretch, nowrap, 0, 0, auto);
}

.__prompt__label {
  margin: 1em 0;
  text-align: center;
}


