@import "../utilities/index";

:global {
  .facebook-button {
    background-color: $blue-facebook;
    border-color: darken($blue-facebook, 5%);
    @include tap-effect($blue-facebook)
  }

  .facebook-button-icon {
    border: none;
    display: inline-block;
    height: 18px;
    vertical-align: -19%;
    line-height: 18px;
    width: 18px;
    padding: 0;
  }

  .facebook-button-text {
    color: white;
    padding-left: 0.6em;
    text-transform: none;
    vertical-align: middle;
    display: inline-block;
  }

  .google-button {
    @include tap-effect(#ea4335)
  }

  .google-button-text {
    padding-left: 0.5em;
  }
}





