@import "../../../styles/utilities/index";
@import "../../../../shared/styles/partials/scroll";

input-validation {
  position: relative;
  display: block;

  height: 0px;
  width: 100%;
}

.__wrapper {
  position: absolute;

  font-size: 0.9em;

  top: 10px;
  left: 5%;
  min-width: 90%;
  z-index: 99;

  padding: $padding-input-validation;
  border-radius: 6px;
  border: 1px solid #bdbdbd;

  box-shadow: 0px 3px 9px 3px rgba(84, 83, 83, 0.46);
  line-height: 1.85;
  background: rgb(254, 254, 254);
}

.__danger {
  background: darken($danger, 10%);
}

.__warning {
  background: $warning;
}

.__primary {
  background: $primary;
}

.__blue-green {
  background: $blue-green-darker
}

.__default {
  border: 1px solid $grey-12;
  background-color: white;
  color: $base;
}

.__success {
  background: $success;
}

.__icon-wrapper {
  display: inline-block;
  vertical-align: middle;

  height: $on-screen-notification-height;
  width: auto;
  padding: calc(#{$on-screen-notification-height/2} - 11px) 8px calc(#{$on-screen-notification-height/2} - 11px) 14px;

  &::before {
    font-size: 1.7em;
  }
}

.__icon {
  margin: 0 0 0 0;
  &::before {
    font-size: 1.7em;
  }
}

.__arrow {
  width: 70px;
  height: 16px;
  overflow: hidden;
  position: absolute;
  left: 20%;
  margin-left: -35px;
  top: -16px;
}
.__arrow:after {
  content: "";
  position: absolute;
  left: 20px;
  width: 25px;
  height: 25px;
  top: 12px;
  box-shadow: 6px 5px 9px -9px rgba(84, 83, 83, 0.35), 5px 6px 9px -9px rgba(84, 83, 83, 0.35);
  transform: rotate(225deg);

  border: 1px solid #bdbdbd;
  background: black;
  background-color: white;
}


