@import "../utilities/index";

:global {
  a {
    position: relative;
    color: $primary;
    text-decoration: none;
    padding: 0.5em 0.7em;
    @include tap-effect($primary);

    &.a--no-side-padding {
      padding: 0.5em 0;
    }

    &:link, &:visited, &:hover, &:active {
      color: $primary;
    }

    &:active {
      color: $primary;
    }

  }
}




