@import "../utilities/_index";
@import "../typography/_font-settings";

html {
  box-sizing: border-box; // border-box everywhere
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); // prevent tap highlight
  -webkit-font-smoothing: antialiased;
  touch-action: manipulation; // ensure fast clicks on ios safari 9, 10 https://webkit.org/blog/5610/more-responsive-tapping-on-ios/

  height: 100%; // full height
  -webkit-overflow-scrolling: auto;
  overflow: hidden;

  //background: -webkit-linear-gradient(30deg, $primary-darker 0%, $primary-medium 100%);
  //background: linear-gradient(30deg, $primary-darker 0%, $primary-medium 100%);
  //background-attachment: fixed;
  background: $primary-medium;
  user-select: none; // prevent annoying select with mouse or touch and hold gesture
}

*, *:before, *:after {
  box-sizing: inherit; // border-box everywhere
  -webkit-tap-highlight-color: inherit; // prevent tap highlight
}

body {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 0, 100%);

  height: 100%; // full height
  overflow: hidden;
  //max-height: 100vh;
  //@include scroll();
  //overflow-y: scroll;

  font-family: $font-headings; // default fonts config
  font-weight: $base-font-weight;
  font-size: $base-font-size;
  @media (min-width: $breakpoint-mobile-med-landscape) { // cap font size increase
    font-size: calc(2px + #{$font-size-min} + #{$breakpoint-mobile-med-landscape/100});
  }

  color: $base;
  background: inherit;

  text-rendering: optimizeLegibility; // enables kerning and optional ligatures, in webkit browser
  -webkit-font-smoothing: antialiased;

  // TODO verify those
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  -ms-content-zooming: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}




