@import "../../../styles/utilities/_index";

$player-width: 100%;
$slido-max-width: 360px;
$slido-height-mobile-small: 300px;
$slido-height-mobile: 400px;
$slido-height-desktop: calc(100vh - 3.5em - #{2*$toolbar-height});

$player-min-height-mobile-small: 15em;
$player-min-height-mobile: 20em;
$player-min-height-desktop: 27em;

main-player {
  @include flexbox(row, center, flex-start, nowrap, 1, 1, auto);

  padding: 0;
  @media (min-width: $breakpoint-laptop-small) {
    padding-left: $sidebar-desktop-width;
  }
}

.__wrapper {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 0, auto);

  background: $grey-13-75;

  width: 100%;
  min-height: calc(100vh - #{2*$toolbar-height});

  @media (min-width: $breakpoint-laptop-small) {
    min-height: calc(100vh - #{1*$toolbar-height});
  }
}

.__item__wrapper {
  @include flexbox(column, center, center, nowrap, 1, 1, auto);
}

.__item {
  position: relative;

  @include flexbox(column, flex-start, stretch, nowrap, 1, 1, auto);
  width: 100%;
  max-width: $breakpoint-laptop-small;
  padding: 0;

  @media (min-width: $breakpoint-tablet) {
    padding: $padding-default;
    flex-flow: row nowrap;
  }
}

.__player {
  display: flex;
  flex-flow: column;

  position: relative;

  margin: 0 0 0 0;

  flex-basis: auto;
  flex-shrink: 0;

  width: 100%;

  @media (min-width: $breakpoint-tablet) {
    margin: 0 1em 0 0;
    width: calc(#{$player-width} - 1em - #{$slido-max-width});
  }

}

.__livestream-player {
  position: relative;

  flex: 0 1;

  width: 100%;
  max-width: 100%;
  overflow: hidden;
  min-height: $player-min-height-mobile-small;
  height: $player-min-height-mobile-small;
  max-height: $player-min-height-mobile-small;

  background: white;

  iframe {
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    min-height: $player-min-height-mobile-small;
    height: $player-min-height-mobile-small;
    max-height: $player-min-height-mobile-small;
  }

  @media (min-width: $breakpoint-mobile-small) {
    min-height: $player-min-height-mobile;
    height: $player-min-height-mobile;
    max-height: $player-min-height-mobile;
    iframe {
      min-height: $player-min-height-mobile;
      height: $player-min-height-mobile;
      max-height: $player-min-height-mobile;
    }
  }

  @media (min-width: $breakpoint-tablet) {
    min-height: $player-min-height-desktop;
    height: $player-min-height-desktop;
    max-height: $player-min-height-desktop;
    iframe {
      min-height: $player-min-height-desktop;
      height: $player-min-height-desktop;
      max-height: $player-min-height-desktop;
    }
  }
}

.__description {
  position: relative;
  flex: 0 0 auto;

  padding: $padding-default;
  @media (min-width: $breakpoint-tablet) {
    padding: $padding-top 0;
  }
  width: 100%;
}

.__slido-embed-wrapper {
  display: flex;
  flex-flow: column;
  flex-basis: auto;
  flex-shrink: 0;
  flex-grow: 1;
  align-items: stretch;

  min-height: $slido-height-mobile-small;
  height: $slido-height-mobile-small;
  max-height: $slido-height-mobile-small;
  max-width: 100%;

  @media (min-height: $breakpoint-mobile-small-height) {
    min-height: $slido-height-mobile;
    height: $slido-height-mobile;
    max-height: $slido-height-mobile;
  }

  @media (min-width: $breakpoint-tablet) {
    min-height: $slido-height-desktop;
    height: $slido-height-desktop;
    max-height: $slido-height-desktop;
    max-width: $slido-max-width;
  }

  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  width: 100%;

  @media (min-width: $breakpoint-tablet) {
    width: calc(#{100% - $player-width});
  }
}

.__slido-embed {
  display: flex;
  flex-flow: column;
  flex-basis: auto;
  flex-shrink: 0;
  flex-grow: 1;
  align-items: stretch;

  width: 100%;

  min-height: $slido-height-mobile-small;
  height: $slido-height-mobile-small;
  max-height: $slido-height-mobile-small;
  max-width: 100%;

  @media (min-height: $breakpoint-mobile-small-height) {
    min-height: $slido-height-mobile;
    height: $slido-height-mobile;
    max-height: $slido-height-mobile;
  }

  @media (min-width: $breakpoint-tablet) {
    min-height: $slido-height-desktop;
    height: $slido-height-desktop;
    max-height: $slido-height-desktop;
  }

  overflow-y: visible;
  -webkit-overflow-scrolling: touch;

  position: relative;

  iframe {
    width: 100%;
    border: none;

    min-height: $slido-height-mobile-small;
    height: $slido-height-mobile-small;
    max-height: $slido-height-mobile-small;
    max-width: 100%;

    @media (min-height: $breakpoint-mobile-small-height) {
      min-height: $slido-height-mobile;
      height: $slido-height-mobile;
      max-height: $slido-height-mobile;
    }

    @media (min-width: $breakpoint-tablet) {
      min-height: $slido-height-desktop;
      height: $slido-height-desktop;
      max-height: $slido-height-desktop;
    }
  }
}




