@import "../../../styles/utilities/index";

auth-toolbar {
  @include flexbox(row, center, center, nowrap, 0, 0, auto);
  position: relative;

  height: $toolbar-height;
  padding: 0.25em 0 0 0;

  background-color: $grey-14;
  border-bottom: 1px solid $grey-13;
  &.--hidden {
    background-color: white;
  }
}

.__back-button {
  position: absolute !important;
  left: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  background-color: $grey-14;
}


@keyframes __appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.__animate {
  opacity: 0;
  animation: __appear $swift-ease-out-timing-function normal 700ms 600ms forwards;
}

.__title {
  font-weight: bold;
  letter-spacing: -0.02em;
  padding-bottom: 3px;
}


