@import "../../../styles/utilities/index";

events {
  @include flexbox(row, center, flex-start, nowrap, 1, 1, auto);

  padding: 0 0 $toolbar-height + 2*$padding-top;
  @media (min-width: $breakpoint-laptop-small) {
    padding-left: $sidebar-desktop-width;
  }
}

.__wrapper {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 0, auto);
  background-color: white;
  width: 100%;
}

.__controls {
  @include flexbox(row, center, center);
  align-self: stretch;
  padding: $padding-default;
  background: $grey-13-75;

  &--align-left {
    justify-content: flex-start;
  }
}

.__list__wrapper {
  @include flexbox(column, center, center, nowrap, 1, 1, auto);
}

.__list {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 1, auto);
  max-width: $breakpoint-laptop-small;
}

.__item {
  position: relative;

  @include flexbox(column, flex-start, flex-start, nowrap, 0, 1, auto);

  @media (min-width: $breakpoint-mobile-med) {
    flex-flow: row nowrap;
  }

  min-height: 16em;
  padding: $padding-top 0 0;
  margin: 0 $padding-side;
  border-bottom: 1px solid $grey-13;

  cursor: pointer;
}

.__item-img {
  flex-shrink: 0;

  width: calc(100vw - #{2*$padding-side});
  height: calc(100vw - #{2*$padding-side});

  @media (min-width: $breakpoint-mobile) {
    width: 19em;
    height: 19em;
  }

  margin-right: 0;

  @media (min-width: $breakpoint-mobile-med-landscape) {
    margin-right: 1em;
  }

  background-size: cover;
}

.__item-description {
  padding: 1em 0;
}

.__item-title {
  font-size: 1.1em;
  font-weight: 500;
  margin-bottom: 0.5em;
}

.__item-date {
  margin: 0.5em 0;
  color: $grey-6;
}

.__load-more {
  margin-top: 2em;
}



