@import "../../../styles/utilities/index";
@import "../../../../shared/styles/partials/scroll";

input-validation {
  position: relative;
  height: 0px;
}

.__wrapper {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;

  padding: $padding-input-validation;

  //border-radius: 5px;
  background: $grey-13;
}

.__danger {
  background: darken($danger, 10%);
}

.__warning {
  background: $warning;
}

.__primary {
  background: $primary;
}

.__blue-green {
  background: $blue-green-darker
}

.__default {
  border: 1px solid $grey-12;
  background-color: white;
  color: $base;
}

.__success {
  background: $success;
}

.__icon-wrapper {
  display: inline-block;
  vertical-align: middle;

  height: $on-screen-notification-height;
  width: auto;
  padding: calc(#{$on-screen-notification-height/2} - 11px) 8px calc(#{$on-screen-notification-height/2} - 11px) 14px;

  &::before {
    font-size: 1.7em;
  }
}

.__icon {
  margin: 0 0 0 0;
  &::before {
    font-size: 1.7em;
  }
}


