@import "../../../styles/utilities/index";

// see app-initial.scss

.--small {
  :global {
    .png-loader {
      animation-name: loader-style__rotate-loader--small;
    }
  }
}

.__wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 98;

  display: flex;
  justify-content: center;
  align-items: center;


  background: rgba(255, 255, 255, 0.74);
  //border: 1px solid rgba(121, 121, 121, 0.51);
  outline: 1px solid rgba(255, 255, 255, 0.70);

  opacity: 0;
  visibility: hidden;

  will-change: opacity, visibility;
  touch-action: none;
  pointer-events: all;

  animation-name: loader-style__show-loader;
  animation-timing-function: $swift-ease-out-timing-function;
  animation-direction: normal;
  animation-duration: 1ms;
  animation-iteration-count: 1;
  animation-delay: 0ms;

  &--not-loading {
    :global {
      animation-name: loader-style__hide-loader;
      animation-timing-function: ease-out;
      animation-direction: normal;
      animation-duration: 800ms;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
    }

  }

  &--loading {
    :global {
      animation-name: loader-style__show-loader;
      animation-timing-function: $swift-ease-out-timing-function;
      animation-direction: normal;
      animation-duration: 200ms;
      animation-iteration-count: 1;
      animation-fill-mode: forwards;
      animation-delay: 0ms;
    }
  }
}


