@import "../../../shared/styles/partials/_scroll";
@import "../../styles/utilities/index";

main {
  @include flexbox(row, center, stretch, nowrap, 1, 1, auto);

  overflow: hidden !important;

  main-sidebar {
    display: none;
  }
  @media (min-width: $breakpoint-laptop-small) {
    main-tabs {
      display: none;
    }
    main-sidebar {
      display: inherit;
    }
  }

  > * {
    @include flexbox(row, center, stretch, nowrap, 1, 1, auto);
  }
}

.__wrapper {
  @include flexbox(column, flex-start, stretch, nowrap, 1, 1, auto);
  position: relative;
}

.__scrolling {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;

  padding-top: $toolbar-height;

  flex-grow: 1;

  @include scroll();
  overflow: scroll;
  overflow-x: hidden;

  $offset: 1*$toolbar-height;

  height: calc(100% - #{$offset});
  min-height: calc(100% - #{$offset});
  max-height: calc(100% - #{$offset});

  @media (min-width: $breakpoint-laptop-small) {
    height: 100%;
    min-height: 100%;
    max-height: 100%;
  }
}

